import { useEffect, useRef } from "react";
import { useTranslation } from "react-i18next";
import { NavigateFunction, useNavigate } from "react-router-dom";
import { Routing } from "routing";
import Styles from "./index.module.css";

const logout = (navigate: NavigateFunction) => () => {
  navigate(`/${Routing.login}`);
};

const goToUserAccount = (navigate: NavigateFunction) => () => {
  navigate(`/${Routing.platformModule}/account`);
};

const goToUsers = (navigate: NavigateFunction) => () => {
  navigate(`/${Routing.platformModule}/user-plan-management`);
};

const goToBills = (navigate: NavigateFunction) => () => {
  navigate(`/${Routing.platformModule}/bills`);
};

interface Props {
  onClickOutside: () => void;
}

export const Menu = (props: Props) => {
  const navigate = useNavigate();
  const { t } = useTranslation();
  const ref = useRef<HTMLDivElement | null>(null);

  const { onClickOutside } = props;

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (ref.current && !ref.current.contains(event.target as Node)) {
        onClickOutside();
      }
    };
    document.addEventListener("click", handleClickOutside, true);
    return () => {
      document.removeEventListener("click", handleClickOutside, true);
    };
  }, [onClickOutside]);

  return (
    <div ref={ref} className={Styles.container}>
      <div className={Styles.section}>
        <p onClick={goToUserAccount(navigate)}>{t("menu.profile")}</p>
      </div>
      <div className={Styles.section}>
        <p onClick={goToUsers(navigate)}>{t("menu.users")}</p>
        <p onClick={goToBills(navigate)}>{t("menu.bills")}</p>
      </div>
      <div className={Styles.section}>
        <p onClick={logout(navigate)}>{t("menu.logout")}</p>
      </div>
    </div>
  );
};
